import React from 'react';
import {NavLink} from 'react-router-dom';
import {inject} from 'mobx-react';
import store from './store.js';

@inject((props) =>{
  return {
    ...props,
    uiStore: store
  }
})
class Header extends React.Component{
  renderNavLi = (data) => {
    const {uiStore} = this.props;
    return (data || []).map((item, index) =>
      (
        <li className={index / 2 === 0 && 'is-middle'}>
          <NavLink to={`${uiStore.routes[index]}`}>{item}</NavLink>
        </li>
      ));
  }
  render () {
    const {uiStore} = this.props;
    return(
      <header id='header'>
        <div className='logo'>
          <span className="icon fa-gem"></span>
        </div>
        <div className='content'>
          <div className='inner'>
            <h1>CRR的技术小栈</h1>
            <p>欢迎你来到前端小菜菜的技术博客。<br/>
            你可以点击<NavLink className='inner-link'>文章列表</NavLink>查看我的文章,也可以给我<NavLink>留言</NavLink>发表你的想法。<br/>
            每天学习前端一点点~ 除了休息日，嘻嘻。
            </p>
          </div>
        </div>
        <nav>
          <ul className={uiStore.liList%2===0 && 'use-middle'}>
            {this.renderNavLi(uiStore.liList)}
          </ul>
        </nav>
      </header>
    );
  }
}

export default Header;